* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Poppins', sans-serif;
    background-color: #0a0e17;
    color: #333;
    text-decoration: none;
    width: 100%;
  }
  
  h1, h2, h3 {
    
    color: #3984d4;
    text-transform: uppercase;
  }
  
  p {
    margin-bottom: 10px;
    color: #ece8e4;
  }
  
  a {
    text-decoration: none;
    color: #007bff;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  .home, .about, .projects, .contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
    padding: 20px;
  }
  
  h1 {
    font-size: 5rem;
    color: #3984d4;
  }
  
  h2 {
    font-size: 4rem;
  }
  
  h3 {
    font-size: 1rem;
  }
  
  p {
    font-size: 1.2rem;
  }
  
  .project-list {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: #2a2c30;
    flex-wrap: wrap; /* Added to handle mobile responsiveness */
  }
  
  .projects {
    
    color: #44423e;
    padding: 20px; /* Added padding for better spacing */
  }
  
  .project {
    margin: 20px 5px;
    background: #afb1b4;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    flex: 1 1 300px; /* Flexbox to adjust size on different screen widths */
    max-width: 100%; /* Ensure the project divs don't overflow */
  }
  
  .navbar {
    display: flex;
    justify-content: right;
    background-color: #0a0e17;
    padding: 10px;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 1000;
    font-weight: bold;
  }
  
  .navbar a {
    margin: 0 15px;
    font-size: 1.2rem;
    transition: color 0.3s ease;
    color: white;
  }
  
  .navbar a:hover {
    color: #007bff;
  }
  
  .hero {
    max-width: 100%;
    margin: auto;
    text-align: left;
    margin-top: 70px;
    margin-bottom: 70px;
  }
  
  .hero h1 {
    font-size: 48px;
    margin-bottom: 10px;
  }
  
  .hero h2 {
    font-size: 24px;
    color: #4fd1c5;
    margin-bottom: 20px;
  }
  
  .hero p {
    margin-bottom: 30px;
    line-height: 1.6;
    font-size: 16px;
  }
  
  .buttons {
    display: flex;
    gap: 15px;
    flex-wrap: wrap; /* Added to make buttons wrap on smaller screens */
  }
  
  .btn {
    padding: 10px 20px;
   border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .btn.primary {
    background-color: #00a3ff;
    color: #fff;
    }
  
  .btn.secondary {
    background-color: #282e38;
    color: #fff;
  }
  
  .btn:hover {
    opacity: 0.9;
  }
  
  /* Media Queries for Mobile Responsiveness */
  @media (max-width: 768px) {
    h1 {
      font-size: 3rem;
    }
  
    h2 {
      font-size: 2.5rem;
    }
  
    h3 {
      font-size: 1.5rem;
    }
  
    p {
      font-size: 1rem;
    }
  
    .home, .about, .projects, .contact {
      padding: 10px;
      height: auto;
      background-color: #0a0e17; /* Allow sections to fit content naturally */
    }
  
    .project-list {
      flex-direction: column; /* Stack projects vertically on mobile */
      align-items: stretch;
    }
  
    .project {
      margin: 10px 0;
    }
    .navbar {
        display: flex;
        justify-content: space-between;
        background-color: #0a0e17;
        padding: 10px;
        position: sticky;
        top: 0;
        width: 100%;
        z-index: 1000;
        font-weight: bold;
      }
      
      .navbar a {
        margin: 0 15px;
        font-size: 1.2rem;
        transition: color 0.3s ease;
        color: white;
      }
      
      .navbar a:hover {
        color: #007bff;
      }
      
      .toggle-btn {
        font-size: 1.5rem;
        color: white;
        cursor: pointer;
        display: none;
        justify-content: left;
        text-align: left; /* Hidden by default */
      }
      
      /* Side navigation styling */
      .nav-links {
        display: flex;
        flex-direction: row;
        transition: transform 0.3s ease; /* Add transition for smooth animation */
      }
      
      .nav-links.mobile {
        position: fixed;
        left: -100%;
        top: 0;
        width: 250px;
        height: 100%;
        background-color: #05090f;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        transition: left 0.3s ease; /* Smooth transition for sliding in */
        transform: translateX(-100%);
      }
      
      .nav-links.mobile.show {
        left: 0;
        transform: translateX(0); /* Slide in the menu */
      }
      
      .nav-links.mobile a {
        margin: 20px 0;
        font-size: 1.5rem;
      }
      
      /* Show the toggle button and change nav to side bar on mobile */
      @media (max-width: 768px) {
        .nav-links {
          display: none;
          text-align: left;
          text-decoration: dashed; /* Hide nav links by default */
        }
      
        .toggle-btn {
          display: block;
          text-align: left; /* Show toggle button on mobile */
        }
      
        .nav-links.mobile {
          display: grid;
          padding-top: 80px;
          padding-bottom: 80px;
          text-align: left;
         text-decoration: dashed;
          text-transform: uppercase;
          transition: all 300ms ease;
          
        }
      }
    }      

    .description {
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      max-height: 4.5em; /* Adjust according to your line height */
      transition: max-height 0.3s ease;
    }
    
    .description.expanded {
      -webkit-line-clamp: unset;
      max-height: none;
    }
     
    .see-more {
      background-color: transparent;
      border: none;
      color: #007bff;
      cursor: pointer;
      text-decoration: underline;
      padding: 0;
      font: inherit;
    }
    