/* Importing the fonts used in the image */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&family=Poppins:wght@400;600&display=swap');

.container {
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: row; 
    background-color: #0a0e17;/* Default to row on larger screens */
   
    
}

.left-half {
    width: 50%;
    height: 100%;
    background-color: #F7F4FF;
    border-radius: 0 50% 50% 0; /* Curved left border */
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    border: 2px rgb(255, 0, 0) dotted;
}

.right-half {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0a0e17;
    padding-left: 20px;
    margin-top: 2px;
    
}

.content {
    color: #FFFFFF;
    text-align: left;
}

.content h1 {
    font-size: 78px;
    font-weight: 700;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    line-height: 80px;
}

.content h2 {
    font-size: 36px;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
    margin-top: 10px;
}



/* Mobile Styles */
@media (max-width: 768px) {
    .container {
        flex-direction: column; /* Stack the columns vertically on mobile */
    }

    .left-half, .right-half {
        width: 100%;
        height: 50vh; /* Adjust the height to make room for both sections */
        border-radius: 0; /* Remove border radius on mobile */
    }

    .left-half {
        order: 2;
        margin-top: 60px; /* Ensure the image comes after the text content */
    }

    .right-half {
        order: 1;
        margin-top: 70px;
        padding: 20px;
        
    }

    .content h1 {
        font-size: 36px; /* Adjust font size for mobile */
    }

    .content h2 {
        font-size: 24px; /* Adjust font size for mobile */
    }

    .button {
        padding: 10px 20px; /* Adjust button size for mobile */
        font-size: 16px;
    }
}
