.contact-section {
    padding: 60px 20px;
    max-width: 900px; /* Increased the maximum width */
    width: 100%; /* Ensures the form takes up full width available */
    margin: auto;
    text-align: left;
  }
  
  .contact-section h2 {
    font-size: 36px;
    margin-bottom: 30px;
    color: #3984d4;
    text-align: center;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
  }
  
  .form-group label {
    margin-bottom: 8px;
    font-size: 16px;
    color: #b0b0b0;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 15px; /* Slightly increased padding for better usability */
    border: 1px solid #3984d4;
    border-radius: 5px;
    background-color: #1c2230;
    color: #ffffff;
    font-size: 16px;
    outline: none;
    width: 100%; /* Ensure inputs take full width */
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    border-color: #00a3ff;
  }
  
  textarea {
    resize: vertical;
    height: 150px;
  }
  
  .btn.primary {
    align-self: flex-start; /* Aligns the button to the left to match the form layout */
    padding: 12px 25px;
    background-color: #00a3ff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .btn.primary:hover {
    opacity: 0.9;
  }
  
  /* Media query to ensure responsiveness on smaller screens */
  @media (max-width: 600px) {
    .contact-section {
      padding: 40px 15px;
    }
  
    .contact-section h2 {
      font-size: 28px;
    }
  
    .form-group input,
    .form-group textarea {
      padding: 12px;
    }
  
    .btn.primary {
      width: 100%;
      text-align: center;
    }
  }
  